@import url(https://fonts.googleapis.com/css?family=Open+Sans:bold,normal);
@import url(https://fonts.googleapis.com/css?family=Quicksand:bold,normal);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* @import url('https://fonts.googleapis.com/css?family=Open+Sans&display=swap'); */

.pink-cover-background {
  background: #BB6BD9;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 100px;
}

.header {
  display: flex;
  justify-content: center;
  color: #4F4F4F;
}

.corona {
  width: 60%;
  max-width: 400px;
  margin-bottom: 100px;
}

.current{
  text-shadow: 0 0 .65px #333, 0 0 .65px #333;
}

.hide {
  display: none;
}

body {
  background-color:#FFFFFF;
  display: flex;
  flex-direction: column;
  color: black;
  font-family: 'Quicksand';
}

.page {
  display: flex;
  flex-direction: column;
  align-items: center;
}

  .page-title {
    font-family: 'Quicksand';
    font-weight: 800;
    font-size: 60px;
    line-height: 82px;
    color: #333333;
    margin: 40px 20px 20px 20px;
    width: 900px;
  }

  .cover-title {
    margin-top: 40px;
    color: white;
  }

  .section-title {
    font-family: 'Quicksand';
    font-weight: bold;
    font-size: 36px;
    line-height: 24px;
  }

.impact-button {
  cursor: pointer;
  font-family: 'Quicksand';
  font-weight: bold;
  color: white;
  background: #BB6BD9;
  border: 3px dotted #FFFFFF;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  font-family: 'Quicksand';
  outline: none;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

  .impact-button-impactpage {
    color: #4F4F4F;
    background: #F2F2F2;
    border: 3px dotted #BDBDBD;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
    border-radius: 8px;
  }

  .unit-text {
    display: inline;
  }

button:hover {
  box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.25);
}

a:link, a:visited, a:active {
  text-decoration: none;
  color: #4F4F4F;
  font-weight: normal;
}

a:hover {
  /* text-decoration: underline; */
  color: #4F4F4F;
}

.beefvstofu {
  width: 60%;
  max-width: 900px;
}

.beefvstofumobile {
  display: none;
}

.home-note {
  margin: 30px 20px 0px 20px;
  font-family: Quicksand;
  font-style: normal;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
}

.home-note-bold {
  display: inline;
  font-weight: bold;
}

.home-note-content {
  display: inline;
}

/* comparisons */

/* .pollution {
  margin: 20px 0px 0px 0px;
} */

.impact-image {
  margin-top: 20px;
  width: 100%;
  max-width: 239px;
}

.same-as {
  font-size: 14px;
  line-height: 21px;
  margin: 20px 0px 10px 0px;
}

.comparison-section {
  /* display: flex; */
  align-items: center;
  justify-content: space-between;
}
  
  .comparison-element {
    display: flex;
    align-items: center;
    max-width: 239px;
    margin-top: 15px;
  }

    .comparison-image {
      height: 50px;
    }

    .comparison-text {
      margin-left: 10px;
    }

      .comparison {
        font-family: 'Quicksand';
        font-weight: normal;
        font-size: 14px;
        line-height: 21px;
      }

      .comparison-support {
        font-size: 14px;
        line-height: 21px;
        color: #828282;
      }

@media only screen and (max-width: 1000px) {

  .header {
    color: #4F4F4F;
    display: flex;
    flex-direction: column;
    background-color: transparent;
    text-align: center;
  }

  .nav {
    color: #4F4F4F;
  }

  .page-title {
    width: 800px;
  }

  .beefvstofu {
    width: 80%;
  }

}

